.footer {
	font-family: 'Open Sans', sans-serif;
}

.footer-top {
	margin-top: 35px;
	    margin-bottom: -40px;
}

.footer-top-schedule {
	color: #787878;
	background-color: #222;
	padding: 37px 25px;
	border-top: 3px solid #ff6d20;
	margin-top: -35px;
}

.footer-top-schedule p {
	    margin-bottom: 16px;
}

.clock-title {
	font-size: 15px;
	text-transform: uppercase;
	position: relative;
	max-width: 135px;
	width: 100%;
	text-align: center;
}

.clock-title:before,
.clock-title:after {
	content: '';
	display: block;
	background-color: #fff;
	width: 3px;
	height: 20px;
	position: absolute;
	top: 0;
}

.clock-title:before {
	left: 0;
}

.clock-title:after {
	right: 0;
}

.to-report-list {
	display: flex;
	padding: 35px;
}

.to-report-item {
	position: relative;
	margin-right: 20px;
	padding-right: 20px;
}

.to-report-item:last-child {
	margin-right: 0;
}

.to-report-item:after {
	content: '';
	display: block;
	width: 4px;
	height: 21px;
	background-color: #fff;
	position: absolute;
	top: 15px;
	right: 0;
}

.to-report-item:first-child:before {
	content: '';
	display: block;
	width: 4px;
	height: 21px;
	background-color: #fff;
	position: absolute;
	top: 15px;
	left: 0;
}
.to-report-item:first-child {
	padding-left: 20px;
}

.to-report-item-link {
	font-size: 16px;
	line-height: 48px;
	font-weight: bold;
	color: #fff;
}

.to-report-item-link:hover {
	color: #fff;
}


.footer-main {
	background: url(../img/footer/bg-middle.png);
	padding: 53px 0px;
}	

.footer-main .clock-title {
	max-width: 180px;
}

.contacts {
	display: flex;
	justify-content: space-around;
	align-items: baseline;
}

.contacts li a {
	color: #787878;
}





.footer-bottom {
	background-color: #1c1c1c;
    padding: 14px 0;
    margin-top: -1px;
}

.footer-logo {
	display: flex;
	justify-content: center;
	align-items: center;
}



