.header {
	font-family: 'PT Sans', sans-serif;
}

.header img {
	display: inline-block;
}

.header-top {
	background-color: #100f0e;
	color: #fff;
	padding: 12px 0;
}

.header-top-wrap {
	justify-content: flex-end;
	position: relative;
}

.header-top-tel,
.header-top-schedule {
	font-size: 12px;
}

.header-top-tel {
	margin-right: 50px;
}

.header-top-tel a {
	color: #fff;
}

.header-top-tel span,
.header-top-schedule span {
	margin-left: 20px;
}

.header-logo {
	position: absolute;
	top: -20px;
	left: 0;
	background-color: #fff;
	max-width: 210px;
	width: 100%;
}

.header-logo img {
	margin: 0 auto;
    width: auto;
    display: block;
}





.header-main-wrap {
	justify-content: flex-end;
	align-items: center;
	font-size: 14px;
	line-height: 16px;
	padding: 10px 0;
}


.header-slogan {
	position: relative;
	padding-right: 25px;
}

.header-slogan:before {
	content: '';
	display: block;
	width: 1px;
	height: 15px;
	background-color: #afaea8;
	position: absolute;
	right: 0;
}

.header-profile {
	align-items: center;
	margin-left: 30px;
}

.header-profile .profile {
	color: #9a9a9a;
	text-decoration: underline;
}

.header-profile .profile:hover {
	text-decoration: none;
}

.header-profile img {
	margin-left: 15px;
}




.header-bottom {
	background-color: #f8f8f8;
	padding: 14px 0;
}

.breadcrumbs {
	display: flex;
}

.breadcrumbs-item {
	margin-right: 10px;
}

.breadcrumbs-item:after {
	content: ':';
	margin-left: 10px;
}

.breadcrumbs-item:last-child:after {
	display: none;
}

.breadcrumbs-item:last-child {
	margin-right: 0;
}

.breadcrumbs-item.active {
	text-decoration: underline;
}

.breadcrumbs-item .breadcrumbs-link {
	color: #9a9a9a;
	font-size: 11px;
	line-height: 20px;
}


