.offer-title {
	font-size: 18px;
	line-height: 30px;
	color: #555555;
	padding-right: 450px;
	margin: 55px 0 30px;
}

.offer-filters-controls {
	justify-content: space-around;
	margin: 30px 0 35px;
}

.btn-site {
	font-family: 'Open Sans', sans-serif;
	font-weight: 600;
	background-color: #ff6d20;
	text-transform: uppercase;
	border-radius: 100px;
	color: #fff;
	line-height: 25px;
	font-size: 11px;
	padding: 6px 30px;
	transition: background 0.3s;
}

.btn-site .fas {
	margin-left: 5px;
}

.btn-site:hover {
	background-color: #fff;
	color: #ff6d20;
	border: 1px solid #ff6d20;
	padding: 5px 29px;
	cursor: pointer;
}

.name-auto .btn-site:hover {
	padding: 5px 0px;
}

.custom-select {
	max-width: 275px;
	width: 100%;
	border-radius: 100px;
}

.card-header .name-auto {
	border-left: 2px solid #ff6d20;
	padding-left: 10px;

	max-width: 300px;
 	width: 100%;
 	margin-right: 22px;
}

.equipment-auto {
	width: 100%
}

.form-check-wrap {
	display: flex;
}

.form-check-label {
	color: #545454;
}

.form-check-label {
	margin-bottom: 0;
	font-size: 12px;
	line-height: 16px;
}

.form-check {
	display: flex;
	align-items: center;
	margin-bottom: 18px;
	position: relative;
	max-width: 235px;
    width: 100%;
}

.form-check:nth-child(2):before,
.form-check:last-child:before {
	 content: '';
	 display: block;
	 width: 27px;
	 height: 27px;
	 background: url(../img/offer/discount.png) no-repeat;
	 position: absolute;
	 top: 4px;
     left: -58px;
}

.name-auto p { 
	margin-bottom: 0; 
	text-transform: uppercase;
}

.name-auto .btn-site {
    padding: 6px 0;
    width: 100%;
    margin-top: 25px;
}

.name-auto .btn-site .far {
	margin-left: 5px;
}

.name-auto a {
	color: #585858;
	margin-top: 15px;
	display: block;
}

.name-auto-report {
	margin-bottom: 0;
}

.name-auto .btn-site,
.name-auto-report {
	text-align: center;
	font-size: 10px;
}

.auto-title {
	font-size: 14px;
}

.little-date {
	font-size: 11px;
}

.equipment-auto span,
.color-auto span {
	font-size: 11px;
	text-transform: uppercase;
}

.btn-link {
	width: 46px;
	height: 43px;
	background-color: #dadada;
	border-radius: 0;
}

.btn-link:hover {
	background-color: #dadada;
	text-decoration: none;
}

.btn-link:hover .fas {
	color: #ff6d20;
}

.btn-link .fas {
	color: #1b1b1b;
}

.card-header {
	background-color: transparent;
	border-top: 1px solid rgba(0,0,0,.125);
	padding: 0 1px;
}

.card {
	border: none;
	margin-bottom: 50px;
}

.card-header {
	align-items:center;
}

.card-body {
	align-items: flex-start;
}

.card-body {
	padding: 22px 11px 0;
}

.name-auto {
 	max-width: 300px;
 	width: 100%;
 	margin-right: 11px;
}

.equipment-auto {
 	padding-left: 80px;
}

.card-header .equipment-auto {	
	max-width: 390px;
	position: relative;
}

.card-header .equipment-auto:after {
	content: '';
	display: block;
	width: 1px;
	height: 35px;
	background-color: #dadada;
	position: absolute;
	top: -5px;
    right: 50px;
} 

.color-auto {
	flex: 1;
	display: flex;
    justify-content: space-between;
    align-items: center;
    
}

.card-body .color-auto {
	margin-top: -20px;
}

.color-selection {
	display: flex;
	flex-wrap: wrap;
	max-width: 250px;
    width: 100%;
    margin-left: 80px;
}

.color-item {
	width: 18px;
    height: 18px;
    border-radius: 100px;
    margin-right: 5px;
    padding: 11px;
    margin-bottom: 3px;
}

.color-item.active-color {
	width: 22px;
    height: 22px;
    border: 2px solid #c3c3c3;
    padding: 0px;
    background: #fff;
}

.active-color {
	position: relative;
}

.active-color:before {
	content: '';
	display: block;
	width: 14px;
	height: 14px;
	background-color: red;
	border-radius: 100px;
	margin: 2px;
}
 
.color-item:hover {
	cursor: pointer;
}

.color-item:last-child {
	margin-right: 0;
}

.red,
.color-item.red.active-color:before {
	background-color: $red;
}

.orange,
.color-item.orange.active-color:before {
	background-color: $orange;
}

.blue,
.color-item.blue.active-color:before {
	background-color: $blue;
}

.green,
.color-item.green.active-color:before {
	background-color: $green;
}

input[type=checkbox], input[type=radio] {display: none;}

input[type=checkbox] + label:before {
    content: "\2713";
    color: transparent;
    display: inline-block;
    border: 1px solid #dadada;
    border-radius: 2px;
    font-size: 14px;
    line-height: 12px;
    margin: 7px 5px 0 0;
    height: 14px;
    width: 14px;
    text-align: center;
    vertical-align: middle;
    -webkit-transition: color ease .3s;
    transition: color ease .3s;
    position: absolute;
    top: 4px;
    left: -10px;
}

input[type=checkbox]:checked + label:before {
    color: #ff6d20;
}

.edit-link {
	text-decoration: underline;
	text-transform: uppercase;
	font-size: 10px;
}

.edit-link:hover {
	color: #ff6d20;
	text-decoration: none;
}

.btn.btn-link .fa-chevron-down {
	display: none;
}

.btn.btn-link .fa-chevron-up {
	display: block;
}

.btn.btn-link.collapsed .fa-chevron-up {
	display: none;
}	

.btn.btn-link.collapsed .fa-chevron-down {
	display: block;
}