
@media (min-width: 1200px) {
	.container {
	    max-width: 1160px;
	}
}

/* 992 - 1200 px */
@media screen and (max-width: 1200px) {
	.to-report-item:after,
	.to-report-item:first-child:before {
		display: none;
	}
	.to-report-list {
		flex-direction: column;
		padding-top: 15px;
	}
	.to-report-item:first-child {
	    padding-left: 0;
	}
	.offer-filters-controls-item {
		margin-right: 10px;
	}
	.offer-filters-controls-item.btn-site {
	    line-height: 17px;
	    text-align: center;
	}
	.offer-filters-controls {
	   margin: 0 0 25px;
	}
	.form-check-wrap {
	    margin-bottom: 10px;
	}
}

/* 768 - 992 px */
@media screen and (max-width: 992px) {
	.footer-top-schedule {
		max-width: 185px;
		margin: -35px auto 0;
	}
	.to-report-list {
	    padding: 15px;
	    align-items: center;
	    margin-bottom: 35px;
	}
	.offer-title {
	    padding-right: 0;
	    text-align: center;
	    margin: 25px 0;
	}
	.offer-filters-controls {
		flex-direction: column;
		align-items: center;
	}
	.offer-filters-controls-item {
		margin-bottom: 5px;
	}
	.offer-filters-controls-item.btn-site {
	    line-height: 14px;
	    max-width: 275px;
	}
	.card-body {
	    flex-direction: column;
	}
	.card-body .name-auto {
		margin: 0 auto 45px;
	}
	.card-header .equipment-auto,
	.card-header .color-auto {
		display: none;
	}
	.card-header .name-auto {
		max-width: 100%;
	}
	.color-edit {
		margin-left: 10px;
	}
	.form-check-wrap {
	    margin-bottom: 0;
	}
}

/* 576 - 768 px */
@media screen and (max-width: 768px) {
	.header-logo {
		display: none;
	}
	.header-top-wrap,
	.header-main-wrap,
	.header-bottom-wrap,
	.breadcrumbs {
		justify-content: center;
	}
	.contacts {
		flex-direction: column;
		align-items: center;
	}
	.contacts-item,
	.to-report-item {
		margin-bottom: 10px;
	}
	.to-report-item {
		margin-right: 0;
    	padding-right: 0;
	}
	.to-report-item-link {
	    line-height: 20px;
	}
	.to-report-item:first-child {
	    text-align: center;
	}
	.form-check-wrap {
	    flex-direction: column;
	    align-items: center;
	    margin-bottom: 15px;
	}
	.color-selection {
		margin-left: 0;
	}
	.color-auto {
	    margin-bottom: 18px;
	    align-self: flex-start;
	}
	.card {
		margin-bottom: 25px;
	}
}

/* 320 - 576 px */
@media screen and (max-width: 576px) {
	.offer-title {
	    font-size: 16px;
	    line-height: 18px;
	    margin: 25px 0;
	}
	.header-top-wrap {
		flex-direction: column;
		justify-content: center;
		max-width: 205px;
		width: 100%;
		margin: 0 auto;
	}
	.header-top-tel,
	.header-top-schedule {
		text-align: left;
	}
	.header-main-wrap {
		flex-direction: column;
	}
	.header-slogan {
		padding-right: 0;
	}
	.header-slogan:before {
		display: none;
	}
	.breadcrumbs {
		flex-direction: column;
	}
	.breadcrumbs-item,
	.header-top-tel {
		margin-right: 0;
	}
	.breadcrumbs-item:after {
		display: none;
	}
	.breadcrumbs-item .fas {
		font-size: 11px;
		margin-left: 10px;
	}
	.breadcrumbs-item:last-child .fas {
		display: none;
	}
	.footer-top-schedule {
		text-align: center;
	}
	.color-auto {
		flex-direction: column;
		align-items: flex-start;
		max-width: 250px;
	    width: 100%;
	    margin: 0 auto;
	}
	.equipment-auto {
	    padding-left: 0;
	}
	.name-auto .btn-site {
	    padding: 6px 25px;
	    line-height: 14px;
	}
	.color-edit {
	    margin-left: 0;
	}
	.name-auto .btn-site:hover {
		padding: 5px 30px;
	}
}

@media screen and (max-width: 425px) {
	.color-auto {
		max-width: 100%;
	    width: 100%;
	}
	.form-check-wrap {
		align-items: center;
	}
	.form-check {
		padding-left: 2rem;
	}
	.form-check:nth-child(2):before, 
	.form-check:last-child:before {
		left: -24px;
	}
	input[type=checkbox]+label:before {
		left: 12px;
	}
	.name-auto img {
		max-width: 100%;
		width: 100%
	}
}

@media screen and (min-width: 576px) {
	.breadcrumbs-item .fas {
		display: none;
	}
}
