@import "variables";

@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700|Source+Sans+Pro:300,400');
@import url('https://fonts.googleapis.com/css?family=Montserrat:200i,300,400&display=swap');

a {
  color: $link;
  transition: color 200ms;
  &:hover {
    color: darken($link, 20);
  }
}

@each $name, $value in $colorList {
  .has-background-#{$name} {
    background-color: $value;
  }
}

@each $name, $value in $fontColors {
  .has-text-#{$name} {
    color: $value;
  }
}

html, body {
  padding: 0;
  margin: 0;
  max-width: 100%;
  overflow-x: hidden;
  font-family: 'Open Sans', sans-serif;
}

*,
*:after,
*:before {
  box-sizing: border-box;
}

body {
  // font-family: 'PT Sans', sans-serif;
  // font-family: 'Open Sans', sans-serif;
}

ul {
  display: block;
  padding: 0;
  margin: 0;
  list-style: none;
}

.d-flex {
  display: flex;
}

.container {
  width: 1160px;
}

.bold {
  font-weight: bold;
}

.brand-color {
  color: #ff6d20;
}

.brand-bg-color {
  background-color: #ff6d20;
}
